/* purgecss start ignore */

body {
  overflow-x: hidden;
}

.home-hero-slider-container .slick-track {
  padding: 1rem 0rem !important;
}

.home-hero-slider-container .slick-slide {
  padding: 0rem 1rem !important;
}

.home-hero-slider-container .slick-center {
  transform: scale(1.02);
  transition: all ease-in 1s;
  transition-timing-function: ease-out;
}

.slider-image {
  max-width: 100% !important;
}

/* arrow customization */

.slick-arrow.slick-prev {
  position: absolute !important;
  left: 0px;
  z-index: 50;
}

.slick-arrow.slick-next {
  position: absolute !important;
  right: 0px;
  z-index: 50;
}

.slick-arrow {
  height: 100% !important;
  border-radius: 0;
  width: 200px !important;
  display: flex !important;
  align-items: center;
  justify-content: center;
}

.slick-arrow:before {
  display: none;
}

.BrainhubCarousel__arrows:focus {
  outline: none;
}

.BrainhubCarousel__arrows:hover:enabled {
  background-color: rgba(0, 0, 0, 0.5) !important;
  border-radius: 50%;
  font-size: 1rem;
}

/* song slider */

.song-slider .slick-arrow {
  display: none !important;
  height: 100% !important;
  border-radius: 0;
  width: auto !important;
}

.song-slider:hover .slick-arrow {
  display: flex !important;
}

.song-slider .slick-arrow.slick-prev {
  left: -20px;
  top: 35%;
}

.song-slider .slick-arrow.slick-next {
  right: 0px;
  top: 35%;
}

.song-card:hover .song-card-overlay {
  display: flex;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.song-card {
  /* transition: all 0.3s ease; */
  position: relative;
  z-index: 1;
}

.song-card:hover {
  transform: scale(1.5);
  z-index: 2;
}

.song-card:hover .song-card-overlay {
  display: flex;
}

.highlight {
  animation: fadeIn 0.5s ease-in-out 1;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.25);
  transform: translateY(-1px);
  transition: all 0.3s ease;
}

@media (max-width: 1200px) {
  .slick-arrow {
    display: none !important;
  }
}

@media (max-width: 600px) {
  .home-hero-slider-container .slick-slide {
    padding: 0rem 0.5rem !important;
  }
}

